:root {
  --main-font: "Archivo", serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body,
ul {
  margin: 0;
  padding: 0;
  color: #fff;
}

body {
  font-family: var(--main-font);
  color: #000;
}

ul {
  list-style: none;
}

a {
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.nav {
  background-color: #ffffff;
  position: fixed;
  width: 100%;
  z-index: 100;
}
.nav__list {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  padding: 21.5px 70px;
  display: flex;
  justify-content: space-between;
}
.nav__logo {
  align-self: center;
}
.nav__item {
  align-self: center;
}
.nav__text {
  margin-right: 20px;
  color: #000;
}
.nav__search {
  position: relative;
}
.nav__searchbox {
  width: 300px;
  height: 37px;
  border: none;
  border-radius: 10px;
  padding: 0px 60px 0px 32px;
  background-color: hsl(0deg, 0%, 86%);
}
.nav__notification {
  display: none;
  font-size: 12px;
  position: absolute;
  background: #fff;
  padding: 5px;
  border-radius: 100%;
  right: 3px;
  top: -17px;
  color: black;
  box-shadow: 0 2px 25px 0px #000;
}
.nav .fa-magnifying-glass {
  position: absolute;
  left: 0;
  color: black;
  margin-top: 10px;
  margin-left: 9px;
}
.nav .fa-basket-shopping {
  position: absolute;
  right: 0;
  color: black;
  padding-left: 23px;
  border-left: 2px solid black;
  margin-top: 10px;
  margin-right: 15px;
}

@media only screen and (max-width: 1023px) {
  .nav {
    display: block;
    background-color: hsl(0deg, 84%, 64%);
    height: 105vh;
    width: 50%;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
    transition: transform 600ms ease-in-out;
  }
  .nav.slide-in {
    transform: translateX(0%);
    transition: transform 600ms ease-in-out;
  }
  .nav__list {
    display: block;
    box-shadow: none;
    padding: 0;
  }
  .nav__logo {
    display: none;
    align-self: center;
  }
  .nav__item {
    align-self: center;
    position: absolute;
    top: 135px;
    left: 35px;
  }
  .nav__text {
    display: block;
    margin-bottom: 25px;
    color: #000;
  }
  .nav__search {
    display: none;
  }
  .nav__searchbox {
    display: none;
  }
  .nav__notification {
    display: none;
  }
  .nav .fa-magnifying-glass {
    display: none;
  }
  .nav .fa-basket-shopping {
    display: none;
  }
  .nav__menu {
    z-index: 9999;
    position: fixed;
    visibility: visible;
    top: 32px;
    left: 90%;
  }
  .nav__menu.open span:first-child {
    transform: rotate(45deg);
  }
  .nav__menu.open span:nth-child(2) {
    opacity: 0;
  }
  .nav__menu.open span:last-child {
    transform: rotate(-45deg);
  }
  .nav__menu span {
    display: block;
    width: 26px;
    height: 2px;
    background-color: #000;
    transform-origin: 4px 1px;
    transition: all 300ms ease-in-out;
  }
  .nav__menu span:not(:last-child) {
    margin-bottom: 4px;
  }
}
