    }
    &__wrapper {
      display: block;
    }
    &__sidebar {
      display: block;
      width: 80vw;
      height: 720px;
    }
    &__section {
    }
    &__category {
    }
    &__links {
    }
    &__items {
    }
    &__text {
    }
    &__divider {
    }
    &__order {
    }
    &__total {
    }
    &__btn {
    }
    &__grid {
      display: block;
    }
    &__container {
    }
    &__pagination {
      li {
      }
      a {
        margin-right: 15px;
      }
    }
    &__name {
    }
    &__image {
      img {
      }
    }
    &__content {
    }
    &__pcs {
    }
    &__price {
    }
    &__quantity {
      i {
      }
    }
    &__number {
    }
  }
}
